 import { Component, OnInit, AfterViewInit, ViewChild } from '@angular/core';
 import {SignatureFieldComponent} from '../signature-field/signature-field.component';
 declare var require: any;
var pdfMake = require('pdfmake/build/pdfmake.js');
var pdfFonts = require('pdfmake/build/vfs_fonts.js');
 pdfMake.vfs = pdfFonts.pdfMake.vfs;

@Component({
  selector: 'app-pdf-maker',
  templateUrl: './pdf-maker.component.html',
  styleUrls: ['./pdf-maker.component.css']
})

export class PdfMakerComponent implements AfterViewInit {

    public signatureData: any;
    public dd: any;
    public options: Object = {};

    @ViewChild(SignatureFieldComponent) child: any;

constructor(){
   
 };

 ngAfterViewInit() {
   this.child.options = { // passed through to szimek/signature_pad constructor
    'minWidth': 1,
    'dotSize': 0.1,
    'canvasWidth': 300,
    'canvasHeight': 200, 
    'maxWidth': 2,
    'backgroundColor': '#f1f1f1'

  };
  }


 createPdf(){
    this.signatureData = this.child.signatureData;
 //console.log("Signature Data : "+ this.signatureData);
    this.dd = {
        content: [
            'First paragraph',
            '16 Nov 2016',
            'Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry\'s standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.',
            '',
            '',
    {
                 image: this.signatureData,
                width: 100,
            },
        ],
          }
         pdfMake.createPdf(this.dd).open();
    console.log('pdf created');
 }

 downloadPdf(){
    pdfMake.createPdf(this.dd).download();
        console.log('pdf downloaded');
     }
 
}
 


