import { Component, ViewChild, forwardRef } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { AngularSignaturePad } from 'angular-signaturepad/signature-pad';

/*
  Generated class for the SignatureField component.
  See https://angular.io/docs/ts/latest/api/core/ComponentMetadata-class.html
  for more info on Angular 2 Components.
*/

@Component({
  selector: 'signature-field',
  templateUrl: 'signature-field.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SignatureFieldComponent),
      multi: true,
    },
  ],
})
export class SignatureFieldComponent implements ControlValueAccessor {

  private options: Object = { // passed through to szimek/signature_pad constructor
    'minWidth': 1,
    'dotSize': 0.1,
    'canvasWidth': 250,
    'canvasHeight': 150, 
    'maxWidth': 2,
    'backgroundColor': '#f1f1f1'

  };
  @ViewChild(AngularSignaturePad) public signaturePad: AngularSignaturePad;

 // public options: Object = {};

 public signatureData: any;

  public _signature: any = null;

  public propagateChange: Function = null;

  get signature(): any {
    return this._signature;
  }

  set signature(value: any) {
    this._signature = value;
  }

  public writeValue(value: any): void {
    if (!value) {
      return;
    }
    this._signature = value;
    this.signaturePad.fromDataURL(this.signature);
  }


  public registerOnChange(fn: any): void {
    //this.propagateChange = fn;
  }

  public registerOnTouched(): void {
    // no-op
  }

  public ngAfterViewInit(): void {
    this.signaturePad.clear();
  }

  public drawBegin(): void {
    //console.log('Begin Drawing');
  }

  public drawComplete(): void {
    this.signature = this.signaturePad.toDataURL('image/png', 0.5);
  }

public save(): void {
  this.signatureData = this.signaturePad.toDataURL('image/png', 0.5);
    // this.signaturePad.clear();
    // this.signature = '';
  }
  public clear(): void {
    this.signaturePad.clear();
    this.signature = '';
  }

 
}



