import { Component, OnInit } from '@angular/core';
import {FormGroup, Validators, FormControl} from '@angular/forms';
import { HttpClient } from '@angular/common/http';
declare var require: any;
//import '/assets/scripts/xml2json.js';
let X2JS = require('./xml2json.js');


@Component({
  selector: 'app-usps-validation',
  templateUrl: './usps-validation.component.html',
  styleUrls: ['./usps-validation.component.css']
})
export class UspsValidationComponent implements OnInit {

 x2js: any;
 originalAddress: any = null;
 addressValidated: any = null;
 uspsResponseJson: any = null;
 uspsResponseXml: any = null;
 form: any;
 xmlFormat: any = null;
 baseUrl: string = 'https://secure.shippingapis.com/ShippingAPI.dll';

    constructor(private http: HttpClient) {
      //  initializing json to xml converter instance
        this.x2js = new X2JS();

        let group: any = {};
        group.streetAddress = new FormControl('3200 Lenox Rd F204', Validators.required);
        group.zip = new FormControl('30324');
        group.state = new FormControl('GA');
        group.city = new FormControl('Atlanta');
        this.form = new FormGroup(group);
    };

    ngOnInit(){

    }

    convertXml2JSon(addressObj: any) {
        return this.x2js.xml_str2json(addressObj);
    }

    convertJSon2XML(addressObj: any) {
       return this.x2js.json2xml_str(addressObj);
    };
    onSubmit() {

        this.originalAddress = {
            'Address1': this.form.value.address2 || '',
            'Address2': this.form.value.streetAddress,
            'City': this.form.value.city,
            'State': this.form.value.state,
            'Zip5': this.form.value.zip,
            'Zip4': ''
        };

        let uspsObj = {
            'AddressValidateRequest': {
                'Revision': '1',
                'Address': this.originalAddress,
                '_USERID': '423SOUTH5942'
            }
        };
        this.xmlFormat = this.convertJSon2XML(uspsObj);
    }
    get_uspsAddress(){
        const requestUrl  =  this.baseUrl + '?API=Verify&XML=' + (this.xmlFormat);
       	this.http.get(requestUrl, {responseType: 'text'}).subscribe((data) => {
	        let response   = this.convertXml2JSon(data);
	        this.addressValidated = response.AddressValidateResponse.Address;
	         },
	         err => {
	             return 'something went wrong';
        });
       };

}


