import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule }   from '@angular/router';
import { NotFoundComponent } from './not-found/not-found.component';
import { PdfMakerComponent } from './pdf-maker/pdf-maker.component';
import { UspsValidationComponent } from './usps-validation/usps-validation.component';
import { SignatureFieldComponent } from './signature-field/signature-field.component';

const appRoutes: Routes = [

     { path: '',    					component: SignatureFieldComponent},
     { path: 'usps',         component: UspsValidationComponent },
      {path: 'signature',               component: SignatureFieldComponent},
     {path: 'pdf',                     component: PdfMakerComponent},
     { path: 'not-found',              component: NotFoundComponent },
     { path: '**',                     redirectTo: 'not-found' },
  ];
export const routing: ModuleWithProviders = RouterModule.forRoot(appRoutes);



