import { NgModule }      from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http';
import { AppBootstrapModule } from './app-bootstrap/app-bootstrap.module';
import { AppComponent } from './app.component';
import { ReactiveFormsModule } from '@angular/forms';

import { routing } from './app.routing';
import { NavbarComponent } from './navbar/navbar.component';
import { NotFoundComponent } from './not-found/not-found.component';
import {OnlyNumber} from './directives/Onlynumber.directive';
import { PdfMakerComponent } from './pdf-maker/pdf-maker.component';
import { SignaturePadModule } from 'angular-signaturepad';
import { UspsValidationComponent } from './usps-validation/usps-validation.component';
import { SignatureFieldComponent } from './signature-field/signature-field.component';
@NgModule({
    imports: [
        BrowserModule,
        AppBootstrapModule,
        ReactiveFormsModule,
        routing,
        HttpClientModule,
     	SignaturePadModule,
    ],
    declarations: [
         AppComponent,
         NavbarComponent,
         NotFoundComponent,
         OnlyNumber,
         PdfMakerComponent,
         UspsValidationComponent,
         SignatureFieldComponent
        
        ],
        providers: [],
        bootstrap: [AppComponent]
})
export class AppModule { }


